.popup_containerf {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.4);
    display: flex;
    justify-content: center;
    align-items: center;
}

.popup_innerf {
    position: relative;
    padding: 32;
    width: 100%;
    max-width: 512px;
    height: 200px;
    background-color: white;
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;

    >h3 {
        font-size: 30px;

    }

    >div {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 37%;

        >.first {
            background-color: #004B84;
            color: white;
            font-size: 15px;
            padding: 12px 20px;
            border: none;
            border-radius: 0.25em;
            margin-top: 25px;

            &:hover {
                cursor: pointer;
                opacity: 0.5;
            }
        }

        >.second {
            background-color: #6e7881;
            color: white;
            font-size: 15px;
            padding: 12px 20px;
            border: none;
            border-radius: 0.25em;
            margin-top: 25px;

            &:hover {
                cursor: pointer;
                opacity: 0.5;
            }
        }
    }
}