.boardList {
    display: grid;
    grid-template-columns: 221px 191px auto;
    padding-left: 35px;
    margin-bottom: 15px;

    >p {
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 20px;
        color: #000000;
    }

}