.devs_list {
    width: 60%;
    margin-top: 34px;
    margin-bottom: 56px;

    >.devs_list_header {
        width: 100%;
        height: 66px;
        background-color: #004B84;
        padding: 10px;
        display: grid;
        grid-template-columns: 150px 140px 120px 150px 290px;
        padding-left: 35px;
        margin-bottom: 15px;

        >p {
            font-style: normal;
            font-weight: 400;
            font-size: 24px;
            line-height: 29px;
            color: #FFFFFF;
            text-align: left;
            align-self: center;
        }
    }

}

.smallList {
    display: grid;
    grid-template-columns: 150px 140px 120px 150px 290px;
    padding-left: 20px;
    width: 100%;


    >p {
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 20px;
        color: #000000;
        padding: 10px 15px;
        text-align: left;

    }

    >a {
        padding: 10px 15px;
    }
}



@media screen and (max-width: 1500px) {
    .devs_list{
        width: 90%;
    }
}