.popup_container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.4);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
}

.popup_inner {
    position: relative;
    padding: 22px;
    width: 100%;
    max-width: 612px;
    height: 200px;
    background-color: white;
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;

    >h3 {
        font-size: 30px;

    }

    >button {
        background-color: #004B84;
        color: white;
        font-size: 15px;
        padding: 12px 20px;
        border: none;
        border-radius: 10px;
        margin-top: 25px;

        &:hover {
            cursor: pointer;
            opacity: 0.5;
        }
    }
}