.test_container {
    width: 100%;
    height: 100%;
    display: flex;
    margin: 0;
    padding: 0;

    >.test_right_part {
        padding: 37px 62px;
        margin-left: 15%;
        width: 80%;

        >.test_header {
            display: flex;
            align-items: center;
            justify-content: space-between;

            >.test_first {
                display: flex;
                align-items: center;

                >a {
                    text-transform: none;

                    >button {
                        display: flex;
                        padding: 10px;
                        background-color: #004B84;
                        border: none;
                        width: 97px;
                        height: 40px;
                        cursor: pointer;

                        >p {
                            font-style: normal;
                            font-weight: 400;
                            font-size: 16px;
                            line-height: 19px;
                            color: #FFFFFF;
                            margin-left: 15px;
                        }
                    }
                }

                >h3 {
                    font-style: normal;
                    font-weight: 400;
                    font-size: 24px;
                    line-height: 29px;
                    color: #004B84;
                    margin-left: 38px;
                }
            }
        }

        >.test_right_name {
            >.test_list {
                display: flex;
                align-items: flex-start;
                justify-content: space-between;
                margin-top: 27px;
                width: 95%;
                >.list_name{
                    width: 40%;
                    >h3 {
                        font-style: normal;
                        font-weight: 400;
                        font-size: 24px;
                        line-height: 29px;
                        color: #004B84;
                        margin-left: 3px;
                        margin-top: 43px;
                        margin-bottom: 25px;
                    }

                }
            }
        }
    }
}